.container {
  display: flex;
  width: 600px;
  box-sizing: border-box;
  background-color: #f0f0f0;
  padding: 10px;
}
.main {
  z-index: 1111;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.backdrop {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.sidebar {
  background-color: #f0f0f0;
  margin: 0;
  width: 220px;
  height: 450px;
  overflow-y: auto;
}
.header {
  width: 560px;
  background-color: #888888c0;
  padding: 10px 20px;
  color: white;
  text-align: center;
  font-size: 20px;
}
