.sidebar .avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.sidebar .name {
  font-weight: bold;
}

.sidebar .status {
  margin-left: 10px;
}

.container .content {
  flex-grow: 1;
  padding: 20px;
}
.btn_item {
  width: 20px;
  height: 20px;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  cursor: pointer;
  right: -5px;
  outline: none;
  border: none;
  top: -5px;
  color: red;
  position: absolute;
  transition: transform 0.25s linear;
}
.btn_item:hover {
  transform: scale(1.3);
}
